body {
    margin: 0;
    padding: 0;
    font-family: sans-serif;
}

.box {
    display: flex;
    align-items: center;
    justify-content: center;
    background-image: linear-gradient(19deg, #21D4FD 0%, #B721FF 100%);
    height: 100vh;
}

.form {
    min-width: 250px;
    max-width: 400px;
    border-radius: 24px;
    padding: 35px;
    background-color: white;
    box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
}

.form h1 {
    text-align: center;
    font-size: 2.5rem;
    margin-top: 35px;
}

.form input[type="text"] {
    margin-top: 30px;
}

.form input[type="email"] {
    margin-top: 10px;
}

.form input[type="password"] {
    margin-top: 10px;
}

input {
    outline: none;
}

.links {
    margin-top: 5px;
    margin-bottom: 5px;
    display: flex;
    flex-wrap: wrap;
    flex-direction: row;
}

.links>a {
    background-color: #E0E0E0;
    border-radius: 12px;
    color: black;
    flex: 1;
    text-align: center;
    padding: 10px;
    text-decoration: none;
    transition: 0.25s;
}

.links>a:hover {
    opacity: 0.6;
}

.form input[type="submit"] {
    background-color: #E0E0E0;
    background-image: linear-gradient(19deg, #21D4FD 0%, #B721FF 100%);
    width: 100%;
    color: white;
    border: none;
    margin-top: 35px;
    cursor: pointer;
    padding: 10px;
    font-size: 1.3rem;
    font-weight: bold;
    border-radius: 12px;
    transition: 0.25s;
}

.form input[type="submit"]:hover {
    opacity: 0.8;
}

.form input[type="text"]:focus,
.form input[type="email"]:focus,
.form input[type="password"]:focus {
    border: 2px #21D4FD solid;
}

.form input[type="text"],
.form input[type="email"],
.form input[type="password"] {
    width: 100%;
    border: none;
    border-radius: 12px;
    font-size: 1rem;
    background-color: gainsboro;
    padding: 15px;
}